/* About Section */
.about-section h2 {
    color: #f38ba8; /* Red for About section headers */
}

.about-section {
    padding: 40px 20px;
    margin: 20px;
    background-color: #181825; /* Darker background for sections */
    border: 4px solid #cba6f7; /* Lavender border */
}

.about-content {
    display: flex;
    flex-direction: column; /* Stacks the avatar and paragraph vertically */
    align-items: center; /* Centers content horizontally */
    justify-content: center; /* Centers content vertically */
    text-align: center;
    gap: 20px; /* Adds space between avatar and paragraph */
}

.pixel-avatar {
    width: 150px;
    height: 150px;
    border: 2px solid #cba6f7; /* Lavender frame */
    border-radius: 8px; /* Rounded corners */
    object-fit: cover;
    transition: box-shadow 0.4s ease, transform 0.4s ease; /* Smooth transition for glow and tilt */
}

/* Subtle glowing shadow on hover */
.tilt-avatar:hover .pixel-avatar {
    box-shadow: 0 0 8px 2px rgba(203, 166, 247, 0.5), /* Subtle outer lavender glow */
                0 0 15px 4px rgba(203, 166, 247, 0.3); /* Less intense glow */
}

/* Languages and Tools Sections */
.languages-section h2 {
    color: #fab387; /* Peach color for section headers */
    margin-bottom: 20px;
}

.tools-section h2 {
    color: #f9e2af; /* Yellow for tools section headers */
    margin-bottom: 20px;
}

.languages-section, .tools-section {
    padding: 40px 20px;
    margin: 20px;
    background-color: #181825;
    border: 4px solid #cba6f7;
}

.languages-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    column-gap: 20px; /* Add spacing between columns */
    gap: 20px; /* Add spacing between rows */
    padding: 10px 30px;
    justify-items: center;
}

/* Grid Styling for Languages and Tools */
.tools-grid {
    display: grid;
    grid-template-columns: repeat(5, minmax(150px, 1fr)); /* 5 equal columns */
    gap: 20px; /* Space between items */
    justify-items: center; /* Center items in their grid areas */
    padding: 10px 30px;
}

/* Card Styling for Languages and Tools */
.language-card, .tool-card {
    background-color: #1e1e2e; /* Mocha background */
    padding: 20px;
    border: 2px solid #cba6f7; /* Lavender border */
    border-radius: 8px;
    text-align: center;
    width: 150px; /* Fixed width for consistent card sizing */
    height: 200px; /* Fixed height */
    transition: box-shadow 0.4s ease, transform 0.4s ease;
}

.language-card img, .tool-card img {
    width: 80px;
    height: 80px;
    margin-bottom: 10px;
}

.language-card p, .tool-card p {
    margin: 0;
    font-size: 14px;
    color: #cdd6f4; /* Text color */
    transition: font-size 0.2s ease;
}

/* Subtle glowing shadow on hover for language and tool cards */
.tilt-language:hover .language-card, .tilt-tool:hover .tool-card {
    box-shadow: 0 0 8px 2px rgba(203, 166, 247, 0.5),
                0 0 15px 4px rgba(203, 166, 247, 0.3);
}

/* Responsive adjustments with media queries */
@media (min-width: 768px) {
    .language-card, .tool-card {
        width: 180px; /* Slightly larger width for medium screens */
        height: 120px; /* Adjust height proportionally */
    }

    .language-card p, .tool-card p {
        font-size: 15px; /* Slightly larger font size on medium screens */
    }
}

@media (min-width: 1024px) {
    .language-card, .tool-card {
        width: 200px; /* Max width for larger screens */
        height: 120px; /* Adjust height proportionally */
    }

    .language-card p, .tool-card p {
        font-size: 16px; /* Moderate increase in font size */
    }
}

/* Contact Section */
.contact-section h2 {
    color: #94e2d5; /* Cyan color for Contact section headers */
}

.contact-section {
    padding: 40px 20px;
    margin: 20px;
    background-color: #181825;
    border: 4px solid #cba6f7;
    box-shadow: 8px 8px #6c7086;
}

.contact-info {
    margin: 20px 0;
}
