/* Global Styles */
@import url('https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap');

body {
    font-family: 'Press Start 2P', cursive;
    /* background: #1e1e2e url('/src/assets/images/background.webp') repeat; */
    background: #1e1e2e;
    margin: 0;
    padding: 0;
    color: #cdd6f4; /* Text color */
    text-align: center;
}

h1, h2 {
    margin: 0;
}

h2 {
    margin-bottom: 20px;
}

a {
    text-decoration: none;
}

ul {
    padding: 0;
}

img {
    max-width: 100%;
}
