.site-header {
    margin: 20px 0;
}

.pixel-title {
    font-size: 36px;
    margin: 20px;
    color: #cba6f7; /* Lavender */
}

.pixel-nav ul {
    list-style-type: none;
    padding: 0;
}

.pixel-nav li {
    display: inline-block;
    margin: 0 10px;
}

.pixel-nav a {
    text-decoration: none;
    color: #cdd6f4; /* Rosewater */
    padding: 10px 20px;
    border: 2px solid #cba6f7; /* Pink border */
    background-color: #181825; /* Light blue background */
    border-radius: 4px;
    box-shadow: 4px 4px #575268; /* Darker shadow */
    transition: all 0.3s;
}

/* Hover styles specific to sections */
.pixel-nav a.about-link:hover {
    background-color: #f38ba8; /* Match About section h2 color */
    color: #1e1e2e;
}

.pixel-nav a.languages-link:hover {
    background-color: #fab387; /* Match Languages section h2 color */
    color: #1e1e2e;
}

.pixel-nav a.tools-link:hover {
    background-color: #f9e2af; /* Match Tools section h2 color */
    color: #1e1e2e;
}

.pixel-nav a.contact-link:hover {
    background-color: #94e2d5; /* Match Contact section h2 color */
    color: #1e1e2e;
}
